
import { Vue } from 'vue-class-component';

class Rune {
  public name: string;
  public value: number;
  public count: number;

  constructor(name: string, value: number, count: number) {
    this.name = name;
    this.value = value;
    this.count = count;
  }
}

export default class App extends Vue {
  private _runeValueMap: Map<string, number> = new Map<string, number>([
    ["Golden Rune [1]", 200], 
    ["Golden Rune [2]", 400], 
    ["Golden Rune [3]", 800], 
    ["Golden Rune [4]", 1200], 
    ["Golden Rune [5]", 1600], 
    ["Golden Rune [6]", 2000], 
    ["Golden Rune [7]", 2500], 
    ["Golden Rune [8]", 3000], 
    ["Golden Rune [9]", 3800], 
    ["Golden Rune [10]", 5000], 
    ["Golden Rune [11]", 6200], 
    ["Golden Rune [12]", 7500], 
    ["Hero’s Rune [1]", 15000], 
    ["Hero’s Rune [2]", 20000], 
    ["Hero’s Rune [3]", 25000], 
    ["Hero’s Rune [4]", 30000], 
    ["Hero’s Rune [5]", 35000], 
    ["Lord’s Rune", 50000]
  ]);

  public runes: Array<Rune> = new Array<Rune>();

  beforeCreate?(): void {
    this._runeValueMap.forEach((value, key) => {
      this.runes.push(new Rune(key, value, 0));
    });
  }

  public getValue(index: number): number {
    const rune = this.runes[index];
    return rune.value * rune.count;
  }

  public getTotalValue(): number {
    return this.runes.reduce((total: number, rune: Rune) => {
      return total + rune.value * rune.count;
    }, 0);
  }

  public clear(index: number): void {
    this.runes[index].count = 0;
  }

  public clearAll(): void {
    this.runes.forEach((rune: Rune) => {
      rune.count = 0;
    });
  }
}
